:root{
    --fuenteHeading: 'PT Sans', sans-serif;
    --fuenteParrafos: 'Open Sans', sans-serif;

    --colorNegro: #000000;
    --colorGris: #757575;
    --colorBlanco: #ffffff;
    --grisclaro: #DFE9F3;
    --colorhtml: #f06529;
    --colorcss: #2965f1;
    --colorjs: #f0db4f;
    --colorphp: #6C78AF;
    --colormysql: #00758F ;
    --colorgit: #6C78AF;
}


html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: var(--fuenteParrafos);
    font-size: 1.6rem;
    line-height: 2;
    background-image: linear-gradient(to top, var(--grisclaro) 0%, var(--colorBlanco) 100%);
}

a {
    text-decoration: none;
}

/* a:hover {
    color: var(--colorPrimario);
}
*/

h1, h2, h3, h4 {
    font-family: var(--fuenteHeading);
    line-height: 1.2;
}

h1 {
    font-size: 4.8rem;
}

img {
    max-width: 100%;
    border: none;
}

.contenedor {
    max-width: 120rem;
    width: 90%;
    margin: 0 auto;
}

.no-margin {
    margin: 0;
}


